const d = document,
w = window,
n = navigator,
c = console.log
export const pwa = () =>{
  if ( 'serviceWorker' in n ) {
    w.addEventListener('load', () => {
      n.serviceWorker.register('./sw.js')
        .then( registration => {
          c(registration)
          c(
            'Service Worker registrado con éxito',
            registration.scope
          )
        })
        .catch( err => c(`Registro de Service Worker fallido`, err) )
    })
  }

  //Activar Notificaciones
  if( w.Notification && Notification.permission !== 'denied' ) {
    Notification.requestPermission(status => {
      console.log(status)
      let n = new Notification('Oreka', {
        body: 'Bienvenidos a Oreka App',
        icon: './icon_128x128.jpeg'
      })
    })
  }

  //Activar Sincronización de Fondo
  if ( 'serviceWorker' in n && 'SyncManager' in w ) {
    function registerBGSync () {
      n.serviceWorker.ready
        .then(registration => {
          return registration.sync.register('github')
            .then( () => c('Sincronización de Fondo Registrada') )
            .catch( err => c('Fallo la Sincronización de Fondo', err) )
        })
    }

    registerBGSync()
  }
}




